@import "_variables.sass";
@import "_classes.sass";

.icon-before-after {
  text-decoration: none !important;
  display: inline-block;
  margin-right: 3px;
  font-size: 16px;
  vert-align: middle;
  padding: 0 3px 2px 3px;
  height: 20px;
  width: 19px; }


.awr-icon.date-element {
  @extend .element;
  &:before {
    //content: "\1F4C5"
    content: "\1F4C6";
    margin-right: 5px;
    margin-left: 5px;
    font-size: 1.3em;
    color: darken(red, 35%); } }

.awr-icon.books-element {
  @extend .bold-element;
  &:before {
    content: "\1F4DA";
    margin-right: 5px;
    font-size: 1.3em;
    color: darken(red, 40%); } }

.awr-icon.edit-element {
  @extend .element;
  font-weight: 600;
  &:after {
    content: "\1F4DD";
    width: 35px;
    margin-left: 10px;
    font-size: 1.5em;
    cursor: pointer;
    padding: 5px;
    text-align: center; }
  &:hover {
    &:after {
      background: lighten(#ccc, 35%);
      border: 1px solid darken(#ccc, 5%);
      border-radius: 5px;
      width: 30px; } } }

.awr-icon.edit-element.icon-left {
  padding-left: 10px;
  &:after {
    display: none; }
  &:before {
    padding: 5px;
    content: "\1F4DD";
    width: 35px;
    margin-right: 20px;
    font-size: 1.5em;
    cursor: pointer;
    text-align: center;
    border: 1px solid white; }
  &:hover {
    &:after {
      display: none; }
    &:before {
      background: lighten(#ccc, 35%);
      border: 1px solid darken(#ccc, 5%);
      border-radius: 5px;
      max-width: 20px !important; } } }


/* text-underline for &:before Is it possible to remove this?*/
/* Yes, if you change the display style to inline-block from default->inline*/
.awr-icon.back-link:hover {
  text-decoration: underline;
  &:before {
    background: lighten($awr-link-color, 20%);
    text-decoration: none !important; } }

.awr-icon.back-link {
  @extend .element;
  @extend .link-element;
  @extend .no-underline;
  &:before {
    @extend .icon-before-after;
    color: white;
    background: $awr-link-color;
    font-size: 16px;
    font-weight: 900;
    border-radius: 50%;
    /*other options: \2190 \21E6*/
    content: "\21B5"; } }

.awr-icon.add-element:hover {
  text-decoration: underline;
  &:before {
    background: lighten($awr-link-color, 20%);
    text-decoration: none !important; } }

.awr-icon.add-element {
  @extend .element;
  @extend .link-element;
  @extend .no-underline;
  vertical-align: middle;
  &:before {
    @extend .icon-before-after;
    color: white;
    padding: 0 2px 4px 2px !important;
    font-size: 16px;
    font-weight: 900;
    border-radius: 26%;
    margin-right: 5px;
    background: $awr-link-color;
    content: "\2795"; } }
