#awrNotifBar.notif {
  display: none;
  width: 100%;
  background-color: lighten(#000000,25%);
  color: #ffffff;
  padding: 10px 0px 2px 0px;
  text-align: center;
  min-height: 10px;
  .content {
    padding: 0;
    margin: 0 auto;
    text-align: left;
    @media (min-width: $mq-tablet-landscape) {
      width: 60%; }
    @media (max-width: $mq-tablet-landscape) {
      width: 90%; } } }
