.awr-basic-table {
  margin-top: 30px;
  font-family: 'Open Sans', sans-serif;
  @media (max-width: 1200px) {
    padding: 5px;
    margin-left: 0;
    margin-right: 0;
    width: 100%; }
  @media (max-width: 650px) {
    .screen-extra {
      display: none; }
    tbody {
      tr {
        height: 80px; } } }
  @media (max-width: 550px) {
    .medium-extra {
      display: none; } }
  @media (max-width: 550px) {
    padding: 0;
    .medium-extra {
      display: none; }
    thead {
      th {
        display: none; }
      th:first-of-type {
        display: block; } }
    tbody {
      tr {
        position: relative;
        th {
          width: 100%;
          height: 100px;
          span.edit-element.name {
              text-overflow: ellipsis;
              overflow-x: hidden !important;
              overflow-y: visible !important;
              height: 35px !important; }
          .mobile-tools {
            padding: 6px 10px 3px 10px;
            display: inline-block;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 36px;
            background: lighten(#4d4c4c, 65%);
            .coll-remove {
              float: right;
              margin-top: 5px; }
            .toggle {
              float: right;
              margin-left: 10px;
              margin-right: 15px; }
            .toggle-group {
              height: 25px;
              label {
                padding: 2px; } }
            .toggle.btn {
              min-height: 25px; } } }
        th .mobile-tools {
          .item-count {
            float: right;
            margin-left: 10px;
            .fa-spinner {
              display: none; } }
          .item-count.loading {
            .fa-spinner {
              display: inline-flex; } } }
        td {
          display: none; } } } }

  .awr-head-row {
    margin-top: 20px;
    h3 {
      font-weight: 500;
      width: 80%;
      display: inline-block;
      font-size: 2.2em; }
    .add-element {
      font-size: 1.1em; } }
  table {
    margin-top: 15px;
    th, td {
      padding-left: 15px !important;
      position: relative; }
    thead {
      background: lighten(#000, 30%);
      th:first-of-type {
        padding-left: 27px !important; }
      th {
        text-align: center;
        color: white; } }
    tbody {
      .mobile-tools {
        display: none; }
      .data {
        padding-top: 16px;
        font-size: 1.2em;
        font-family: 'Open Sans', sans-serif; }
      th {
        .name {
          display: inline-block; }
        input[type="text"] {
          display: none; }
        .btn-group {
          display: none; } }
      .edit-mode {
        th {
          @media (min-width: 550px) and (max-width: 1200px) {
            display: inline-flex;
            .btn-group {
              width: 100%;
              button {
                width: 50%;
                border-top-right-radius: 0;
                border-top-left-radius: 0; } } } }
        th {
          width: 100%;
          display: inline-block;
          .name {
            display: none; }
          input[type="text"] {
            width: 70%;
            height: 33px;
            @media (min-width: 550px) and (max-width: 1200px) {
              width: 100%;
              //border: 1px solid
              border: 1px solid #ddd;
              border-bottom: none;
              border-top-right-radius: 4px;
              border-top-left-radius: 4px; }
            @media (max-width: 550px) {
              width: 73%; }
            @media (max-width: 450px) {
              width: 70%; }
            @media (max-width: 400px) {
              width: 62%; }
            /* border: none; */
            outline: none;
            padding: 2px 5px 2px 5px;
            display: inline-block; }
          .btn-group {
            display: block;
            float: right;
            height: 30px; } } }
      td {
        .fa-spin {
          display: none; } }
      td.loading {
        .fa-spin {
          position: absolute;
          right: 15px;
          top: 23px;
          display: inline-block !important; } } } } }

