#basicModal {
  min-width: 320px;
  .modal-header {
    overflow-x: hidden;
    .modal-title {
      @media (max-width: $mq-mobile-landscape) {
        padding: 0;
        display: initial;
        text-align: center; } }
    .close {
      position: absolute;
      right: 15px;
      top: 15px; } }
  .modal-body {
    padding: 15px 30px 15px 30px; } }
