.awr-tag {
  .result-container {
    position: relative;
    margin-top: -2px;
    .results.out {
      display: none; }
    .results.in {
      display: inline-block;
      z-index: 1000; }
    .results {
      padding: 5px 0 5px 0;
      position: absolute;
      display: none;
      background: darken(#fff,2%);
      box-shadow: 2px 2px 2px #888888;
      border-bottom-left-radius: 3px;
      border-bottom-right-radius: 3px;
      width: 99%;
      .item {
        padding: 3px 5px 2px 5px;
        width: 100%;
        display: inline-block;
        font-weight: 500;
        font-size: 14px;
        cursor: pointer;
        strong {
          margin: 0 5%;
          width: 50%; } }
      .item:hover, .item.hover {
         color: darken(#fff,29%); } } }
  .tag-container {
    display: block !important;
    padding: 3px;
    overflow-y: visible;
    width: 100%;
    border-radius: 2px;
    border: solid 1px #ccc;
    input[type="text"] {
      display: inline-block;
      height: 30px;
      border: none;
      font-size: 14px;
      padding: 0;
      margin-left: 3px; }
    input[type="text"]:focus {
      border: none !important;
      outline: none; }
    .tag {
      margin: 2px;
      padding: 3px 5px 0 5px;
      background: darken(#fff, 6%);
      border-radius: 5px;
      font-size: 14px;
      border: 1px solid darken(#fff, 29%);
      white-space: nowrap;
      display: inline-block;
      height: 30px;
      .text {
        padding: 0; }
      span.fa-remove {
        border-left: 1px solid darken(#fff, 49%);
        margin-left: 4px;
        padding-left: 4px;
        cursor: pointer; }
      span.fa-remove:hover {
        color: lighten(#000, 60%); } } } }
